import kotlinx.browser.localStorage
import kotlinx.js.console
import models.GameState
import persistence.Serializers.GameStateSerializer
import persistence.decode
import persistence.encode
import react.ReducerInstance
import react.useReducer
import redux.GameEvent
import redux.reduce

fun useGameState(): ReducerInstance<GameState, GameEvent> {
  return useReducer(
    { state, event ->
      state.reduce(event).also { newState ->
        console.log(newState.shareLink())
        localStorage.setItem("gameState", GameStateSerializer.encode(newState))
      }
    },
    null,
  ) {
    val stateString = localStorage.getItem("gameState")
    try {
      if (stateString != null) {
        GameStateSerializer.decode(stateString)
      } else {
        GameState(emptyList())
      }
    } catch (e: Throwable) {
      console.log(e, "Deserialize exception")
      GameState(emptyList())
    }
  }
}

fun GameState.shareLink(): String =
  "?g=${GameStateSerializer.encode(this).base64Encoded.uriEncoded}"
