import bootstrap.Container
import bootstrap.Fluidity
import bootstrap.Nav
import bootstrap.Navbar
import csstype.ClassName
import csstype.MaxHeight
import csstype.Overflow
import emotion.styled.styled
import kotlinx.browser.localStorage
import models.AppDispatches
import models.AppState
import models.Dialog
import models.GameState
import models.Player
import org.w3c.dom.get
import persistence.Serializers.GameStateSerializer
import persistence.decode
import react.FC
import react.Props
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.li
import react.dom.html.ReactHTML.p
import react.dom.html.ReactHTML.ul
import react.useMemo
import react.useState
import redux.GameEvent
import redux.PlayerEvent

external interface AppProps : Props

fun nameValidator(
  dispatches: AppDispatches, onValid: (String) -> GameEvent
): Validator = Validator { name ->
  when {
    name.isBlank() -> ValidationResult.Error("Can't be blank")
    name.length > 20 -> ValidationResult.Error("Max length is 20")
    name.contains('/') -> ValidationResult.Error("Can't contain \"/\"")
    else -> ValidationResult.Success { dispatches.sendGameEvent(onValid(name)) }
  }
}

val CardContainer = div.styled { _, _ ->
  maxHeight = "calc(100vh - 100px)".unsafeCast<MaxHeight>()
  overflow = Overflow.scroll
}

val App = FC<AppProps> {
  var appState: AppState by useState(AppState())

  val (gameState, sendGameEvent) = useGameState()

  val appDispatches = //useMemo(appState, sendGameEvent) {
    AppDispatches(
      sendGameEvent = sendGameEvent,
      showDialog = { appState = appState.withDialog(it) },
      closeDialog = { appState = appState.withDialog(null) },
    )
  val newNameValidator = useMemo(appDispatches) {
    nameValidator(appDispatches) { GameEvent.NewPlayer(it) }
  }

  useSuspendOnce {
    val savedState = localStorage["gameState"]
    val state = if (savedState == null) {
      GameState(emptyList())
    } else {
      try {
        GameStateSerializer.decode(savedState)
      } catch (e: Exception) {
        GameState(emptyList())
      }
    }
    sendGameEvent(GameEvent.LoadGame(state))
  }

  Navbar {
    Container {
      Nav {
        Navbar.Brand { +"Perahtzee!" }
        Navbar.Toggle {}
        Navbar.Collapse {
          className = ClassName("justify-content-end")
          Nav.Link {
            +"New Player"
            onClick = {
              appDispatches.showDialog(Dialog.NewPlayerDialog)
            }
          }
          Nav.Link {
            +"New Game"
            onClick = {
              appDispatches.showDialog(Dialog.NewGameDialog)
            }
          }
        }
      }
    }
  }

  appState.let { state ->
    state.dialogs.forEach { (t, dialog) ->
      AppModal {
        show = t == state.shownDialog
        title = dialog.title
        when (dialog) {
          is Dialog.NewGameDialog -> {
            p { +"This can't be undone!" }
            action("Same Players") {
              appDispatches.sendGameEvent(
                GameEvent.LoadGame(GameState(gameState.players.map { Player(it.name) }))
              )
              appDispatches.closeDialog()
            }
            action("New Players") {
              appDispatches.sendGameEvent(
                GameEvent.LoadGame(GameState(emptyList()))
              )
              appDispatches.closeDialog()
            }
          }
          is Dialog.NewPlayerDialog -> {
            form = AppModalForm(
              type = ModalInputType.TEXT, label = "Enter name:",
              placeholder = "Pennie", focusOnInput = true,
              validator = newNameValidator,
            )
            formAction("OK")
          }
          is Dialog.PlayerDialog -> {
            form = AppModalForm(
              type = ModalInputType.TEXT, label = "Edit name:",
              initialValue = dialog.playerName, placeholder = "Pennie", focusOnInput = false,
              validator = nameValidator(appDispatches) {
                GameEvent.UpdatePlayer(
                  dialog.playerIndex, PlayerEvent.UpdateName(it)
                )
              },
            )
            action("Delete", "danger") {
              appDispatches.sendGameEvent(GameEvent.RemovePlayer(dialog.playerIndex))
              appDispatches.closeDialog()
            }
            formAction("OK")

            if (dialog.stillNeeds.isNotEmpty()) {
              p { +"${dialog.playerName} still needs:" }
              ul {
                dialog.stillNeeds.forEach { rollName ->
                  li { +rollName }
                }
              }
            }
          }
          is Dialog.ScoreDialog -> {
            form = AppModalForm(
              type = ModalInputType.NUMBER,
              label = if (dialog.currentScore == null) "Score:" else "Edit score:",
              initialValue = dialog.currentScore?.toString(), placeholder = "0 - 30",
              focusOnInput = true
            ) { input ->
              when (val score = input.toIntOrNull()) {
                null -> ValidationResult.Error("Has to be a number")
                0 -> ValidationResult.Success {
                  appDispatches.playerDispatches(dialog.playerIndex).sendPlayerEvent(
                    PlayerEvent.CloseVariableRoll(dialog.roll)
                  )
                }
                in 1..30 -> ValidationResult.Success {
                  appDispatches.playerDispatches(dialog.playerIndex).sendPlayerEvent(
                    PlayerEvent.ScoreVariableRoll(dialog.roll, score)
                  )
                }
                else -> ValidationResult.Error("Out of range (0-30)")
              }
            }
            if (!dialog.rollIsOpen) {
              action("Clear", "secondary") {
                appDispatches.playerDispatches(dialog.playerIndex)
                  .sendPlayerEvent(PlayerEvent.ClearVariableRoll(dialog.roll))
                appDispatches.closeDialog()
              }
            }
            action("X it out", "danger") {
              appDispatches.playerDispatches(dialog.playerIndex)
                .sendPlayerEvent(PlayerEvent.CloseVariableRoll(dialog.roll))
              appDispatches.closeDialog()
            }
            formAction("OK")
          }
          is Dialog.BonusYahtzeeDialog -> {
            form = AppModalForm(
              type = ModalInputType.NUMBER, label = "How many bonus Yahtzees?",
              initialValue = "${dialog.currentCount}", placeholder = "0+",
              focusOnInput = true,
            ) { input ->
              val count = input.toIntOrNull()
              when {
                count == null -> ValidationResult.Error("Has to be a number")
                count < 0 -> ValidationResult.Error("Has to be positive")
                else -> ValidationResult.Success {
                  appDispatches.playerDispatches(dialog.playerIndex).sendPlayerEvent(
                    PlayerEvent.SetYahtzeeBonuses(count)
                  )
                }
              }
            }
            formAction("OK")
          }
        }
        dispatches = appDispatches
      }
    }
  }
  CardContainer {
    Container {
      className = ClassName("border")
      fluid = Fluidity.SMALL
      ScoreCard {
        this.gameState = gameState
        this.appDispatches = appDispatches
      }
    }
  }
}
