package models

import csstype.ClassName
import redux.PlayerEvent

private val Roll.columnValue
  get() = when (this) {
    is Roll.OpenRoll -> ColumnValue.Open
    is Roll.ScoredRoll -> ColumnValue.Score(score)
    is Roll.ClosedRoll -> ColumnValue.Closed
  }

data class GameState(
  val players: List<Player>
) {
  val stateString
    get() = players.joinToString { "${it.name}:${it.score}" }

  val upperHeader: ScoreCardRow
    get() = ScoreCardRow.UpperHeader(players.map { it.upperBonusProgress })

  private fun scoreRow(
    name: String,
    onClick: ((Int, Player, PlayerDispatches) -> Unit)? = null,
    extract: (Int, Player) -> Roll,
  ) = ScoreCardRow.ScoreRow(
    name,
    scores = players.mapIndexed { i, player -> extract(i, player).columnValue },
    onClick = onClick,
  )

  private fun totalRow(
    name: String, extractScore: (Player) -> Int?
  ) = ScoreCardRow.ScoreRow(
    name, scores = players.map {
      when (val score = extractScore(it)) {
        null -> ColumnValue.Open
        else -> ColumnValue.Score(score)
      }
    }, className = ClassName("table-secondary")
  )

  fun upperRow(base: Int): ScoreCardRow = scoreRow("$base's", { _, _, dispatches ->
    dispatches.sendPlayerEvent(PlayerEvent.IncrementUpperRoll(base))
  }) { _, player: Player ->
    player.upperRolls[base - 1].roll
  }

  val upperRawScores: ScoreCardRow
    get() = totalRow("Upper Total") { it.upperRawScore }

  val upperBonuses
    get() = totalRow("Bonus") { it.upperBonus }

  val upperScores
    get() = totalRow("Upper Total (with Bonus)") { it.upperScore }

  fun lowerScore(
    roll: FixedValueRoll
  ) = scoreRow(roll.displayName, { _, _, dispatches ->
    dispatches.sendPlayerEvent(PlayerEvent.StepFixedRoll(roll))
  }) { _, player -> player.fixedValueRolls[roll]!! }

  fun lowerScore(roll: VariableValueRoll) =
    scoreRow(roll.displayName, { playerIndex, player, dispatches ->
      val (currentScore, isOpen) = when (val r = player.variableValueRolls[roll]) {
        is Roll.ScoredRoll, is Roll.ClosedRoll -> Pair(r.score, false)
        is Roll.OpenRoll -> Pair(null, true)
        else -> Pair(null, false)
      }
      dispatches.showDialog(
        Dialog.ScoreDialog(
          playerIndex, player.name, roll, isOpen, currentScore
        )
      )
    }) { _, player -> player.variableValueRolls[roll]!! }

  val lowerScores
    get() = totalRow("Lower Total") { it.lowerScore }

  val yahtzeeBonusRow: ScoreCardRow
    get() = scoreRow("Yahtzee! Bonus", onClick = { playerIndex, player, dispatches ->
      if (player.hasYahtzee) {
        dispatches.showDialog(
          Dialog.BonusYahtzeeDialog(
            playerIndex, player.name, player.bonusYahtzees
          )
        )
      }
    }) { _, player ->
      when {
        player.fixedValueRolls[FixedValueRoll.YAHTZEE] is Roll.ClosedRoll -> Roll.ClosedRoll
        player.bonusYahtzees == 0 -> Roll.OpenRoll
        else -> Roll.ScoredRoll(player.bonusYahtzeeScore)
      }
    }
}
