package models

import kotlin.reflect.KClass

private val defaultDialogs = mapOf(
  Dialog.NewGameDialog::class to Dialog.NewGameDialog,
  Dialog.NewPlayerDialog::class to Dialog.NewPlayerDialog,
  Dialog.PlayerDialog::class to Dialog.PlayerDialog(-1, ""),
  Dialog.ScoreDialog::class to Dialog.ScoreDialog(-1, "", VariableValueRoll.CHANCE),
  Dialog.BonusYahtzeeDialog::class to Dialog.BonusYahtzeeDialog(-1, "", 0),
)

class AppState(
  val gameState: GameState? = null,
  val dialogs: Map<KClass<out Dialog>, Dialog> = defaultDialogs,
  val shownDialog: KClass<out Dialog>? = null
) {
  fun withState(newGameState: GameState?) = AppState(
    newGameState,
    dialogs,
    shownDialog,
  )

  inline fun <reified DialogT : Dialog> withDialog(dialog: DialogT?): AppState = when (dialog) {
    null -> AppState(gameState, dialogs)
    else -> AppState(
      gameState,
      if (dialogs.containsKey(DialogT::class)) {
        dialogs.mapValues { (t, current) -> if (t == DialogT::class) dialog else current }
      } else {
        dialogs + mapOf(DialogT::class to dialog)
      },
      DialogT::class,
    )
  }
}
