package models

import csstype.ClassName

sealed interface ScoreCardRow {
  data class UpperHeader(val progresses: List<Int>) : ScoreCardRow
  data class ScoreRow(
    val name: String,
    val scores: List<ColumnValue>,
    val className: ClassName? = null,
    val onClick: ((Int, Player, PlayerDispatches) -> Unit)? = null,
  ) : ScoreCardRow

  object LowerHeader : ScoreCardRow
}
