package models

data class Player(
  val name: String,
  val upperRolls: List<UpperRoll> = buildList {
    for (base in 1..6) {
      add(UpperRoll(base))
    }
  },
  val fixedValueRolls: Map<FixedValueRoll, Roll> = FixedValueRoll.values()
    .associateWith { Roll.OpenRoll },
  val variableValueRolls: Map<VariableValueRoll, Roll> = VariableValueRoll.values()
    .associateWith { Roll.OpenRoll },
  val bonusYahtzees: Int = 0,
) {
  init {
    require(upperRolls.size == 6) {
      "Wrong number of upper rolls: $upperRolls"
    }
  }

  val upperRawScore: Int by upperRolls::totalScore
  val upperBonus: Int? = when {
    upperRawScore >= 63 -> 35
    upperRolls.all { it.roll is Roll.ScoredRoll } -> 0
    else -> null
  }
  val upperScore: Int
    get() = upperRawScore + (upperBonus ?: 0)

  val upperBonusProgress: Int
    get() = upperRolls.mapIndexed { index, upperRoll ->
      (upperRoll.count?.minus(3) ?: 0) * (index + 1)
    }.sum()

  val hasYahtzee: Boolean
    get() = fixedValueRolls[FixedValueRoll.YAHTZEE] is Roll.ScoredRoll

  val bonusYahtzeeScore: Int
    get() = if (hasYahtzee) bonusYahtzees * 100 else 0

  val lowerScore: Int
    get() = fixedValueRolls.totalScore + variableValueRolls.totalScore + bonusYahtzeeScore

  val score: Int
    get() = upperScore + lowerScore

  val stillNeeds: List<String>
    get() = upperRolls
      .filter { it.roll is Roll.OpenRoll }
      .map { "${it.base}'s" } +
        fixedValueRolls
          .filterValues { it is Roll.OpenRoll }
          .keys.map { it.displayName } +
        variableValueRolls
          .filterValues { it is Roll.OpenRoll }
          .keys.map { it.displayName }
}
