package bootstrap

var BootstrapFormProps.noValidate: Boolean?
  get() = asDynamic().noValidate.unsafeCast<Boolean?>()
  set(value) {
    asDynamic().noValidate = value
  }

var BootstrapFormControlProps.autoFocus: Boolean?
  get() = asDynamic().autoFocus.unsafeCast<Boolean?>()
  set(value) {
    asDynamic().autoFocus = value
  }

var BootstrapFormControlProps.required: Boolean?
  get() = asDynamic().required.unsafeCast<Boolean?>()
  set(value) {
    asDynamic().required = value
  }
