import react.ReactNode
import react.table.ColumnBuilder
import react.table.HeaderProps
import react.table.SimpleColumn

external interface ColumnWithFooter<D: Any, V> : SimpleColumn<D, V> {
  @JsName("Footer")
  var footer: String

  @JsName("Footer")
  var footerFunction: (HeaderProps<D, V>) -> ReactNode
}

fun <D : Any, V> ColumnBuilder<D>.columnWithFooter(
  block: ColumnWithFooter<D, V>.() -> Unit
) = column<V> {
  this.unsafeCast<ColumnWithFooter<D, V>>().block()
}
