package redux

import models.GameState
import models.Player

fun GameState.reduce(event: GameEvent) = when (event) {
    is GameEvent.LoadGame -> event.loadedState
    is GameEvent.NewPlayer -> this.copy(
      players = players + Player(name = event.name)
    )
    is GameEvent.RemovePlayer -> this.copy(
      players = players.filterIndexed { index, _ -> index != event.index }
    )
    is GameEvent.UpdatePlayer -> this.copy(
      players = players.mapIndexed { index, player ->
        if (index == event.playerIndex) player.reduce(event.playerEvent) else player
      }
    )
  }
