package persistence

fun <T> Decoder<T>.decode(string: String): T {
  val (decoded, leftovers) = decodeAndReturnLeftovers(string)
  check(leftovers.isEmpty()) {
    "Leftovers should be empty (were \"$leftovers\")"
  }
  return decoded
}

fun interface Decoder<T> {
  fun decodeAndReturnLeftovers(string: String): Pair<T, String>
}