package models

sealed interface Dialog {
  val title: String

  object NewGameDialog : Dialog {
    override val title: String = "New Game?"
  }

  object NewPlayerDialog : Dialog {
    override val title: String = "New Player"
  }

  data class PlayerDialog(
    val playerIndex: Int,
    val playerName: String,
    val stillNeeds: List<String> = emptyList(),
  ) : Dialog {
    override val title: String by ::playerName
  }

  class ScoreDialog(
    val playerIndex: Int,
    val playerName: String,
    val roll: VariableValueRoll,
    val rollIsOpen: Boolean = true,
    val currentScore: Int? = null
  ) : Dialog {
    override val title: String
      get() = "${roll.displayName} ($playerName)"
  }

  class BonusYahtzeeDialog(
    val playerIndex: Int,
    val playerName: String,
    val currentCount: Int,
  ) : Dialog {
    override val title: String
      get() = "Bonus Yahtzees ($playerName)"
  }
}
