package redux

import models.Player
import models.Roll

fun Player.reduce(event: PlayerEvent) = when (event) {
  is PlayerEvent.UpdateName -> copy(name = event.name)
  is PlayerEvent.IncrementUpperRoll -> copy(
    upperRolls = upperRolls.mapIndexed { index, roll ->
      if (index == event.base - 1) roll.next() else roll
    }
  )
  is PlayerEvent.StepFixedRoll -> copy(
    fixedValueRolls = fixedValueRolls.updateValue(event.roll) {
      when (it) {
        is Roll.OpenRoll -> Roll.ScoredRoll(event.roll.score)
        is Roll.ScoredRoll -> Roll.ClosedRoll
        is Roll.ClosedRoll -> Roll.OpenRoll
      }
    }
  )
  is PlayerEvent.ScoreVariableRoll -> copy(
    variableValueRolls = variableValueRolls.updateValue(event.roll) {
      Roll.ScoredRoll(event.score)
    }
  )
  is PlayerEvent.ClearVariableRoll -> copy(
    variableValueRolls = variableValueRolls.updateValue(event.roll) {
      Roll.OpenRoll
    }
  )
  is PlayerEvent.CloseVariableRoll -> copy(
    variableValueRolls = variableValueRolls.updateValue(event.roll) {
      Roll.ClosedRoll
    }
  )
  is PlayerEvent.SetYahtzeeBonuses -> copy(bonusYahtzees = event.bonuses)
}

private fun <K, V> Map<K, V>.updateValue(key: K, update: (V) -> V) =
  mapValues { (k, v) -> if (k == key) update(v) else v}
