package persistence

import models.FixedValueRoll
import models.GameState
import models.Player
import models.Roll
import models.UpperRoll
import models.VariableValueRoll

object Serializers {
  private const val ENCODINGS = "0123456789abcdefghijklmnopqrstuv"

  object UpperRollsSerializer : Serializer<List<UpperRoll>> {
    override fun encodeTo(builder: StringBuilder, value: List<UpperRoll>) {
      for (roll in value) {
        builder.append(roll.count ?: "?")
      }
      builder.append(':')
    }

    override fun decodeAndReturnLeftovers(string: String): Pair<List<UpperRoll>, String> {
      val rolls = mutableListOf<UpperRoll>()
      var base = 1
      for (c in string.substringBefore(":")) {
        val count = c.digitToIntOrNull()
        rolls += UpperRoll(base++, count)
      }
      return Pair(rolls.toList(), string.substringAfter(':', ""))
    }
  }

  object FixedRollsEncoder : Serializer<Map<FixedValueRoll, Roll>> {
    override fun encodeTo(builder: StringBuilder, value: Map<FixedValueRoll, Roll>) {
      FixedValueRoll.values().forEach { roll ->
        builder.append(
          when (value[roll]) {
            null, is Roll.OpenRoll -> "!"
            is Roll.ScoredRoll -> 0
            is Roll.ClosedRoll -> 1
          }
        )
      }
    }

    override fun decodeAndReturnLeftovers(string: String): Pair<Map<FixedValueRoll, Roll>, String> {
      require(string.length >= FixedValueRoll.values().size) {
        "String \"$string\" not long enough to decode fixed values. " +
            "Should be ${FixedValueRoll.values().size}"
      }
      val rolls = FixedValueRoll.values().mapIndexed { i, roll ->
        when (string[i].digitToIntOrNull()) {
          0 -> roll to Roll.ScoredRoll(roll.score)
          1 -> roll to Roll.ClosedRoll
          else -> roll to Roll.OpenRoll
        }
      }.toMap()
      return Pair(rolls, string.substring(FixedValueRoll.values().size))
    }
  }

  object VariableRollsEncoder : Serializer<Map<VariableValueRoll, Roll>> {

    override fun encodeTo(builder: StringBuilder, value: Map<VariableValueRoll, Roll>) {
      VariableValueRoll.values().forEach { variableRoll ->
        val pseudoScore: Int = when (val roll = value[variableRoll]) {
          is Roll.ClosedRoll -> 1
          is Roll.ScoredRoll -> 2 + roll.score
          else -> roll?.score ?: 0
        }
        builder.append(ENCODINGS[pseudoScore])
      }
    }

    override fun decodeAndReturnLeftovers(string: String): Pair<Map<VariableValueRoll, Roll>, String> {
      require(string.length >= VariableValueRoll.values().size) {
        "String \"$string\" not long enough to decode variable values. " +
            "Should be ${VariableValueRoll.values().size}"
      }
      val rolls = VariableValueRoll.values().mapIndexed { i, variableRoll ->
        variableRoll to when (val pseudoScore = ENCODINGS.indexOf(string[i])) {
          -1, 0 -> Roll.OpenRoll
          1 -> Roll.ClosedRoll
          else -> Roll.ScoredRoll(pseudoScore - 2)
        }
      }.toMap()
      return Pair(rolls, string.substring(VariableValueRoll.values().size))
    }
  }

  object NameSerializer : Serializer<String> {
    override fun encodeTo(builder: StringBuilder, value: String) {
      builder.append(value).append('/')
    }

    override fun decodeAndReturnLeftovers(string: String): Pair<String, String> = Pair(
      string.substringBefore('/'),
      string.substringAfter('/', "")
    )
  }

  object BonusEncoder : Serializer<Int> {
    override fun encodeTo(builder: StringBuilder, value: Int) {
      require(value in ENCODINGS.indices) {
        "Bonuses ($value) out of range [0, ${ENCODINGS.length})"
      }
      builder.append(ENCODINGS[value])
    }

    override fun decodeAndReturnLeftovers(string: String): Pair<Int, String> {
      val value = ENCODINGS.indexOf(string[0])
      check(value >= 0)
      return Pair(value, string.substring(1))
    }
  }

  val PlayerSerializer: Serializer<Player> =
    EncodeDecode.on<Player, String, NameSerializer>(NameSerializer) { it.name }
      .thenSerializing(UpperRollsSerializer) { it.upperRolls }
      .thenSerializing(FixedRollsEncoder) { it.fixedValueRolls }
      .thenSerializing(VariableRollsEncoder) { it.variableValueRolls }
      .thenSerializing(BonusEncoder) { it.bonusYahtzees }
      .transforming { (a, bonuses) ->
        val (b, variableRolls) = a
        val (c, fixedRolls) = b
        val (name, upperRolls) = c
        Player(name, upperRolls, fixedRolls, variableRolls, bonuses)
      }

  object GameStateSerializer: Serializer<GameState> {
    override fun encodeTo(builder: StringBuilder, value: GameState) {
      builder.append(value.players.size)
      for (player in value.players) {
        builder.append('$')
        PlayerSerializer.encodeTo(builder, player)
      }
    }

    override fun decodeAndReturnLeftovers(string: String): Pair<GameState, String> {
      val playerCount = checkNotNull(string.substringBefore('$').toIntOrNull()) {
        "Should start with <n>$..., was \"$string\""
      }
      var current = string.substring(1  )
      val players = mutableListOf<Player>()
      for (i in 1..playerCount) {
        check(current.startsWith('$')) {
          "Missing player delimiter $ (current = $current)"
        }
        val (player, leftovers) = PlayerSerializer.decodeAndReturnLeftovers(current.substring(1))
        players += player
        current = leftovers
      }
      return Pair(GameState(players.toList()), current)
    }
  }
}
