package persistence

interface EncodeDecode<I, O> : Encoder<I>, Decoder<O> {
  companion object {
    inline fun <T, R, S> on(
      delegate: S, crossinline extract: (T) -> R
    ): EncodeDecode<T, R> where S : Encoder<R>, S : Decoder<R> {
      val outerEncoder = Encoder<T> { builder, value ->
        delegate.encodeTo(builder, extract(value))
      }
      val outerDecoder = Decoder { string ->
        val (decoded, leftovers) = delegate.decodeAndReturnLeftovers(string)
        Pair(decoded, leftovers)
      }
      return object : EncodeDecode<T, R>, Encoder<T> by outerEncoder, Decoder<R> by outerDecoder {}
    }
  }
}

typealias Serializer<T> = EncodeDecode<T, T>

inline fun <T, R, V, S> EncodeDecode<T, R>.thenSerializing(
  serializer: S, crossinline extract: (T) -> V
): EncodeDecode<T, Pair<R, V>> where S : Encoder<V>, S : Decoder<V> {
  val composedEncoder = Encoder<T> { builder, value ->
    this@thenSerializing.encodeTo(builder, value)
    serializer.encodeTo(builder, extract(value))
  }
  val composedDecoder = Decoder { string ->
    val (value, leftovers) = this@thenSerializing.decodeAndReturnLeftovers(string)
    val (decoded, moreLeftovers) = serializer.decodeAndReturnLeftovers(leftovers)
    Pair(Pair(value, decoded), moreLeftovers)
  }
  return object : EncodeDecode<T, Pair<R, V>>, Encoder<T> by composedEncoder,
      Decoder<Pair<R, V>> by composedDecoder {}
}

inline fun <T, R> EncodeDecode<T, R>.transforming(
  crossinline transform: (R) -> T
): Serializer<T> {
  val decoder = Decoder { string ->
    val (value, leftovers) = this@transforming.decodeAndReturnLeftovers(string)
    Pair(transform(value), leftovers)
  }
  return object : Serializer<T>, Encoder<T> by this, Decoder<T> by decoder {}
}