package models

interface Scoreable {
  val score: Int?
}

val Iterable<Scoreable>.totalScore: Int
  get() = sumOf { it.score ?: 0 }

val Map<*, Scoreable>.totalScore: Int
  get() = values.totalScore

sealed class Roll : Scoreable {
  override val score: Int?
    get() = null

  object OpenRoll : Roll()
  data class ScoredRoll(override val score: Int) : Roll()
  object ClosedRoll : Roll()
}

class UpperRoll constructor(
  val base: Int,
  val count: Int? = null,
) : Scoreable {
  init {
    require(count == null || count in 0..5) {
      console.error(count)
      "Count $count out of range [0, 5]"
    }
  }
  val roll : Roll
    get() = when (count) {
      null -> Roll.OpenRoll
      0 -> Roll.ClosedRoll
      else -> Roll.ScoredRoll(base * count)
    }

  override val score: Int? by roll::score

  fun next(): UpperRoll = when(count) {
    null -> UpperRoll(base, 1)
    0 -> UpperRoll(base, null)
    5 -> UpperRoll(base, 0)
    else -> UpperRoll(base, count + 1)
  }
}
