import bootstrap.Button
import bootstrap.Form
import bootstrap.Modal
import bootstrap.autoFocus
import bootstrap.noValidate
import bootstrap.required
import csstype.ClassName
import kotlinx.browser.document
import kotlinx.js.ReadonlyArray
import models.AppDispatches
import org.w3c.dom.HTMLInputElement
import react.FC
import react.PropsWithChildren
import react.dom.html.ReactHTML.br
import react.useMemo
import react.useState

private const val MODAL_INPUT_ID = "perahtzee-modal-input"

data class ModalAction(
  val label: String,
  val variant: String,
  val onClick: (formSubmit: () -> Unit) -> Unit,
)

fun interface Validator {
  operator fun invoke(input: String): ValidationResult
}

sealed interface ValidationResult {
  class Success(internal val submit: () -> Unit) : ValidationResult
  class Error(internal val error: String) : ValidationResult
}

object DefaultValidator : Validator {
  override fun invoke(input: String) = ValidationResult.Success {}
}

val AppModal = FC<AppModalProps> { props ->
  var validationError by useState<String>()

  val submit: () -> Unit = useMemo(
    props.form?.validator, props.dispatches
  ) {
    submitter@{
      validationError = null
      val inputElement =
        document.getElementById(MODAL_INPUT_ID)?.unsafeCast<HTMLInputElement>() ?: return@submitter
      val validate: Validator = props.form?.validator ?: DefaultValidator
      when (val validationResult = validate(inputElement.value)) {
        is ValidationResult.Error -> validationError = validationResult.error
        is ValidationResult.Success -> {
          validationResult.submit()
          props.dispatches.closeDialog()
        }
      }
    }
  }
  Modal {
    show = props.show ?: false
    centered = true
    Modal.Header {
      props.title.ifNotNull { title ->
        Modal.Title { +title }
      }
      Button {
        type = "button"
        className = ClassName("btn-close")
        +"×"
        onClick = {
          validationError = null
          props.dispatches.closeDialog()
        }
      }
    }

    Modal.Body {
      props.form?.let { form ->
        Form {
          noValidate = true
          onSubmit = { formEvent ->
            formEvent.apply {
              preventDefault()
              stopPropagation()
            }
            submit()
          }
          Form.Group {
            form.label.ifNotNull { Form.Label { +it } }
            Form.Control {
              autoFocus = form.focusOnInput
              required = true
              id = MODAL_INPUT_ID
              type = form.type.htmlType
              form.placeholder.ifNotNull { placeholder = it }
              form.initialValue.ifNotNull { defaultValue = it }
              if (validationError != null) {
                isInvalid = true
              }
            }
            Form.Control.Feedback {
              type = "valid"
              +"Looks good!"
            }
            Form.Control.Feedback {
              type = "invalid"
              +(validationError ?: "Looks bad man")
            }
          }
        }
        br {}
      }
      +props.children
    }

    if (props.allActions.isNotEmpty()) {
      Modal.Footer {
        props.allActions.forEach { action ->
          Button {
            +action.label
            variant = action.variant
            onClick = { action.onClick(submit) }
          }
        }
      }
    }
  }
}

enum class ModalInputType(internal val htmlType: String) { TEXT("text"), NUMBER("number") }

data class AppModalForm(
  val type: ModalInputType,
  val label: String? = null,
  val initialValue: String? = null,
  val placeholder: String? = null,
  val focusOnInput: Boolean = false,
  val validator: Validator,
)

external interface AppModalProps : PropsWithChildren {
  var show: Boolean?
  var title: String?
  var actions: ReadonlyArray<ModalAction>?

  var form: AppModalForm?

  var dispatches: AppDispatches
}

val AppModalProps.allActions: ReadonlyArray<ModalAction>
  get() = actions ?: emptyArray()

inline fun AppModalProps.action(
  label: String,
  variant: String = "primary",
  noinline onClick: () -> Unit,
) {
  action(ModalAction(label, variant) { onClick() })
}

inline fun AppModalProps.formAction(
  label: String,
  variant: String = "primary",
) {
  action(ModalAction(label, variant) { submit -> submit() })
}

fun AppModalProps.action(action: ModalAction) {
  val newActions = allActions.toMutableList()
  newActions += action
  actions = newActions.toTypedArray()
}

