package redux

import models.FixedValueRoll
import models.VariableValueRoll

sealed interface PlayerEvent {
  data class UpdateName(val name: String) : PlayerEvent
  data class IncrementUpperRoll(val base: Int) : PlayerEvent
  data class StepFixedRoll(val roll: FixedValueRoll) : PlayerEvent
  data class ScoreVariableRoll(val roll: VariableValueRoll, val score: Int) : PlayerEvent
  data class ClearVariableRoll(val roll: VariableValueRoll) : PlayerEvent
  data class CloseVariableRoll(val roll: VariableValueRoll) : PlayerEvent
  data class SetYahtzeeBonuses(val bonuses: Int) : PlayerEvent
}