package models

import redux.GameEvent
import redux.PlayerEvent

interface AppDispatches {
  fun sendGameEvent(gameEvent: GameEvent)
//  fun showDialog(dialog: Dialog)
  fun showDialog(dialog: Dialog)
  fun closeDialog()

  fun playerDispatches(playerIndex: Int): PlayerDispatches =
    object : AppDispatches by this, PlayerDispatches {
      override fun sendPlayerEvent(playerEvent: PlayerEvent) {
        sendGameEvent(GameEvent.UpdatePlayer(playerIndex, playerEvent))
      }
    }

  companion object {
    operator fun invoke(
      sendGameEvent: (GameEvent) -> Unit = {},
//      showDialog: (Dialog) -> Unit = {},
      showDialog: (Dialog) -> Unit = {},
      closeDialog: () -> Unit = {}
    ) =
      object : AppDispatches {
        override fun sendGameEvent(gameEvent: GameEvent) = sendGameEvent(gameEvent)
//        override fun showDialog(dialog: Dialog) = showDialog(dialog)
        override fun showDialog(dialog: Dialog) = showDialog(dialog)
        override fun closeDialog() = closeDialog()
      }

  }
}

interface PlayerDispatches : AppDispatches {
  fun sendPlayerEvent(playerEvent: PlayerEvent)
}
