package bootstrap

import csstype.ClassName
import react.FC
import react.PropsWithChildren
import react.PropsWithClassName
import react.dom.html.ReactHTML.div

val Container = FC<BootstrapContainerProps> { props ->
  div {
    val classBuilder = StringBuilder("container")
    props.fluid?.let {
      classBuilder.append("-${it.suffix}")
    }
    className = ClassName(classBuilder.toString())
    +props.children
  }
}

enum class Fluidity(internal val suffix: String) {
  ALWAYS("fluid"),
  SMALL("sm"),
  MEDIUM("md"),
  LARGE("lg"),
  X_LARGE("xl"),
  XX_LARGE("xxl"),
}

external interface BootstrapContainerProps : PropsWithChildren, PropsWithClassName {
  var fluid: Fluidity?
}
