import bootstrap.Table
import csstype.ClassName
import csstype.Position
import csstype.ZIndex
import csstype.px
import emotion.react.css
import kotlinx.js.jso
import models.AppDispatches
import models.ColumnValue
import models.Dialog
import models.FixedValueRoll
import models.GameState
import models.ScoreCardRow
import models.VariableValueRoll
import react.FC
import react.Props
import react.create
import react.dom.html.ReactHTML.b
import react.dom.html.ReactHTML.div
import react.dom.html.ReactHTML.span
import react.dom.html.ReactHTML.tbody
import react.dom.html.ReactHTML.td
import react.dom.html.ReactHTML.tfoot
import react.dom.html.ReactHTML.th
import react.dom.html.ReactHTML.thead
import react.dom.html.ReactHTML.tr
import react.table.Column
import react.table.RenderType
import react.table.TableCell
import react.table.TableOptions
import react.table.columns
import react.table.useTable
import react.useMemo
import reacttablesticky.useSticky

external interface ScoreCardProps : Props {
  var gameState: GameState
  var appDispatches: AppDispatches
}

val ScoreCard = FC<ScoreCardProps> { props ->
  val gameState = props.gameState
  val cols: Array<Column<ScoreCardRow, ColumnValue>> = useMemo(
    gameState.stateString
  ) {
    columns {
      columnWithFooter {
        id = "categories"
        header = "Perahtzee!"
        accessorFunction = { row: ScoreCardRow ->
          when (row) {
            is ScoreCardRow.UpperHeader -> "Upper Scores"
            is ScoreCardRow.ScoreRow -> row.name
            is ScoreCardRow.LowerHeader -> "Lower Scores"
          }
        }
        footer = "Grand Total"
      }
      gameState.players.forEachIndexed { index, player ->
        columnWithFooter<ScoreCardRow, ColumnValue> {
          id = "player-$index"
          header = player.name
          accessorFunction = { row: ScoreCardRow ->
            when (row) {
              is ScoreCardRow.UpperHeader -> ColumnValue.Progress(
                row.progresses[index]
              )
              is ScoreCardRow.ScoreRow -> row.scores[index]
              is ScoreCardRow.LowerHeader -> ColumnValue.Skip
            }
          }
          cellFunction = { cellProps ->
            when (val value = cellProps.value) {
              is ColumnValue.Skip, is ColumnValue.Open -> span.create()
              is ColumnValue.Progress -> span.create {
                if (value.progress < 0) {
                  +"${value.progress}"
                } else {
                  +"+${value.progress}"
                }
              }
              is ColumnValue.Score -> span.create { +"${value.score}" }
              is ColumnValue.Closed -> span.create { b { +"×" } }
            }
          }
          footerFunction = { b.create { +"${player.score}" } }
        }
      }
    }.unsafeCast<Array<Column<ScoreCardRow, ColumnValue>>>()
  }
  val tableData = useMemo(gameState) {
    val rows: MutableList<ScoreCardRow> = mutableListOf(gameState.upperHeader)
    for (base in 1..6) {
      rows += gameState.upperRow(base)
    }
    rows += listOf(
      gameState.upperRawScores, gameState.upperBonuses, gameState.upperScores,
      ScoreCardRow.LowerHeader, gameState.lowerScore(VariableValueRoll.THREE_OF_A_KIND),
      gameState.lowerScore(VariableValueRoll.FOUR_OF_A_KIND),
      gameState.lowerScore(FixedValueRoll.FULL_HOUSE),
      gameState.lowerScore(FixedValueRoll.SMALL_STRAIGHT),
      gameState.lowerScore(FixedValueRoll.LARGE_STRAIGHT),
      gameState.lowerScore(VariableValueRoll.CHANCE), gameState.lowerScore(FixedValueRoll.YAHTZEE),
      gameState.yahtzeeBonusRow,
      gameState.lowerScores,
    )
    rows.toTypedArray()
  }
  val tableInstance = useTable(jso<TableOptions<ScoreCardRow>> {
    columns = cols
    data = tableData
  }, useSticky)

  div {
    className = ClassName("table-responsive overflow-scroll")
    Table {
      +tableInstance.getTableProps()
      className = ClassName("table table-bordered text-center")
      thead {
        className = ClassName("thead header")
        tableInstance.headerGroups.forEach { headerGroup ->
          tr {
            className = ClassName("tr table-primary")
            +headerGroup.getHeaderGroupProps()
            headerGroup.headers.forEachIndexed { index, header ->
              th {
                +header.getHeaderProps(propGetter = { props, _ ->
                  props.apply {
                    style?.zIndex = ZIndex(4)
                  }
                })
                className = ClassName("th table-borderless")
                onClick = index.takeIf { it != 0 }.ifNotNull {
                  {
                    props.appDispatches.showDialog(
                      Dialog.PlayerDialog(
                        index - 1,
                        gameState.players[index - 1].name,
                        gameState.players[index - 1].stillNeeds,
                      )
                    )
                  }
                }
                +header.render(RenderType.Header)
              }
            }
          }
        }
      }


      tbody {
        +tableInstance.getTableProps()
        className = ClassName("body align-middle")
        tableInstance.rows.forEach { row ->
          tableInstance.prepareRow(row)
          tr {
            +row.getRowProps()
            var onCellClick: ((Int) -> Unit)? = null
            className = row.original.let { row ->
              if (row is ScoreCardRow.ScoreRow) {
                onCellClick = { index ->
                  row.onClick?.invoke(
                    index,
                    props.gameState.players[index],
                    props.appDispatches.playerDispatches(index)
                  )
                }
                row.className
              } else {
                ClassName("table-info")
              }
            }
            if (row.original is ScoreCardRow.LowerHeader) {
              val cell = row.cells[0]
              td {
                +cell.getCellProps()
                colSpan = row.cells.size
                +cell.render(RenderType.Cell)
              }
            } else {
              row.cells.unsafeCast<Array<TableCell<ScoreCardRow, ColumnValue>>>()
                .forEachIndexed { cellIndex, cell ->
                  td {
                    +cell.getCellProps()
                    onClick = cellIndex.takeIf { it > 0 }?.let { i ->
                      { onCellClick?.invoke(i - 1) }
                    }
                    +cell.render(RenderType.Cell)
                  }
                }
            }
          }
        }
      }
      tfoot {
        className = ClassName("tfoot footer")
        tableInstance.footerGroups.forEach { footerGroup ->
          tr {
            className = ClassName("tr table-primary")
            footerGroup.headers.forEach { column ->
              td {
                +column.getHeaderProps()
                className = ClassName("td")
                css {
                  position = Position.sticky
                  bottom = 0.px
                }
                +column.render(RenderType.Footer)
              }
            }
          }
        }
      }
    }
  }
}

private fun ZIndex(z: Int) = z.unsafeCast<ZIndex>()
