inline fun <T, R> T?.ifNotNull(block: (T) -> R): R? =
  this?.let { block(it) }

external fun encodeURIComponent(s: String): String

val String.uriEncoded: String
  get() = encodeURIComponent(this)

external fun btoa(s: String): String

val String.base64Encoded: String
  get() = btoa(this)

@JsName("atob")
external fun decodeBase64(s: String): String

fun String.decodeBase64(): String = decodeBase64(this)

@JsName("decodeURIComponent")
external fun decodeUri(s: String): String

fun String.decodeUri(): String = decodeUri(this)
